.Card {
  background-color: white;
  padding: 64px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.11), 0 5px 15px 0 rgba(0, 0, 0, 0.08);
  box-sizing: border-box;
}

.button {
  background-color: #249bc4;
  color: white;
  font-size: 20px;
  font-weight: bold;
  margin-top: 20px;
  margin-left: 50px;
  
}

.sub-header {
  text-align: center;

}

.drop {
  text-align: center;
  display: flex;
  justify-content: center;
}

.Dropzone {
  height: 100px;
  width: 100px;
  background-color: #fff;
  border: 2px dashed rgb(187, 186, 186);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 16px;
}

.Icon {
  
  height: 64px;
  width: 64px;
}

.FileInput {
  display: none;
}

.Highlight {
  background-color: rgb(188, 185, 236);
}

